/*! rivet-uits - @version v1.1.0 */

input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
input:not([type]),
textarea,
select {
    display: block;
    width: 100%;
    border-radius: $xxs;
    background-color: inherit;
    /**
     * Making the decision here to stay with 1px border on form element,
     * but increasing the color contrast to meet WCAG requirements.
     *
     * See the WCAG requirement explanation here:
     * https://www.w3.org/WAI/GL/low-vision-a11y-tf/wiki/Contrast_(Minimum)#SC_Text
     *
     * Also, refer to Github issue for new WCAG 2.0 requirements for
     * non-text interactive element:
     *
     * https://github.com/w3c/wcag21/issues/10
     */
    border: 1px solid $color-black--700;
    padding: $xs;
    height: $xl;
    line-height: 1;

    /**
     * Prefixes get rid of browser default select element styles.
     */
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none;
}

textarea {
    height: $spacing-unit * 15;
    line-height: 1.5;
}

input[type="color"]:disabled,
input[type="date"]:disabled,
input[type="datetime"]:disabled,
input[type="datetime-local"]:disabled,
input[type="email"]:disabled,
input[type="month"]:disabled,
input[type="number"]:disabled,
input[type="password"]:disabled,
input[type="search"]:disabled,
input[type="tel"]:disabled,
input[type="text"]:disabled,
input[type="time"]:disabled,
input[type="url"]:disabled,
input[type="week"]:disabled,
input:not([type]):disabled,
textarea:disabled,
select:disabled {
    background-color: $color-black--100;
    cursor: not-allowed;
}

input[type="color"]:focus,
input[type="date"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="email"]:focus,
input[type="month"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="time"]:focus,
input[type="url"]:focus,
input[type="week"]:focus,
input:not([type]):focus,
textarea:focus,
select:focus {
    outline: none;
    /* stylelint-disable */
    box-shadow: 0 0 0 $xxs/2 $color-background,
                0 0 0 $xxs $color-action;
    /* stylelint-enable */
    transition: box-shadow .2s ease;
}

label {
    display: inline-block;
    font-size: $ts-14;
    line-height: 1;
    font-weight: 700;
    margin-bottom: $xs;
}

fieldset {
    border: none;
    margin: 0;
    padding: 0;
}

legend {
    margin: 0;
    font-weight: 400;
    display: block;
}

select {
    // background-color: $color-background;
    background-image: url("data:image/svg+xml;base64,");
    background-position: right $sm center;
    background-size: $sm $sm;
    background-repeat: no-repeat;
    padding-right: $xl;
    padding-left: $xs;

    /**
     * Prefixes get rid of browser default select element styles.
     */
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none;
}

select[multiple] {
    height: $xxl * 2;
    background-image: none;
    padding-right: $xs;
}

select::-ms-expand {
    display: none;
}
