.card {
  background: #FFFFFF;
  border: 1px solid #E1E1E1;
  box-sizing: border-box;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.05);
  border-radius: 6px;

  &s {
    box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.05);
    border: 1px solid #E1E1E1;
    border-radius: 6px;

    .card {
      border: none;
      box-shadow: none;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      border-top: 1px solid #ddd;

      .rvt-display-flex {
        align-items: baseline;
      }
    }

    .card:last-child {
      border-bottom-left-radius: 6px;
      border-bottom-right-radius: 6px;
    }
    .card:first-child {
      border-top-left-radius: 6px;
      border-top-right-radius: 6px;
      border-top: none;
    }

    .rvt-collapsible {
      .card {
        border-radius: 0;

        .card__title:first-child {
          display: none;
        }
      }
      .rvt-collapsible__content:last-child .card {
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
      }
    }

    .rvt-collapsible__title {
      border-top: 1px solid #ddd;

      button {
        padding: .5em .25em .5em 1em;

        &:focus {
          box-shadow: none;
        }
      }
    }
  }


  &__banner {
    margin-left: -1.5em;
    margin-right: -1.5em;
    margin-top: -1.5em;
    margin-bottom: 1em;

    overflow: hidden;
    max-height: 110px;

    img {
      max-width: 100%;
    }
  }

  &__highlight--green {
    color: $color-alert-green;
  }

  &__highlight--red {
    color: $color-alert-orange;
  }

  &__links {
    svg {
      margin-right: .25em;
    }

    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }

  }
}

.card-stack[aria-expanded='false'] {
  margin-top: 1em;
  .card {
    margin-top: 0;
    position: relative;
    z-index: 2;

    &:nth-child(3),
    &:nth-child(2) {
      height: 12px;
      border-radius: 0 0 5px 5px;
      padding: 0;
      box-shadow: none;
      border: none;
      * {
        display: none;
      }
    }
    &:nth-child(3) {
      margin: 0 20px;
      background: rgba(255,255,255,.4);
      box-shadow: 0 0 3px #eaeaea;
      z-index: 0;
    }

    &:nth-child(2) {
      margin: 0 10px;
      background: rgba(255,255,255,1);
      box-shadow: 0 0 7px #e5e5e5;
      z-index: 1;
    }
  }
}

.card-stack[aria-expanded='true'] {
  margin-top: 1em;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.05);

  .card {
    box-shadow: none;
    border-bottom: 1px solid #e5e5e5;
    border-radius: 6px 6px 0 0;
    margin-top: 0;
    position: relative;
    z-index: 2;

    &:nth-child(3),
    &:nth-child(2) {
      height: auto;
      padding: 1.5rem;
      box-shadow: none;
    }

    &:nth-child(3) {
      margin: auto;
      background: #ffffff;
      box-shadow: none;
      z-index: 0;
      border-radius: 0 0 6px 6px;
    }

    &:nth-child(2) {
      margin: auto;
      background: #ffffff;
      box-shadow: none;
      z-index: 0;
      border-bottom: 0;
      border-top: 0;
      border-radius: 0;
    }
  }
}